  background-image: url(../../img/header_desktop.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%; }
  @media (min-resolution: 2dppx) {
    .intro {
      background-image: url(../../img/header_desktop@2.jpg); } }
  @media (max-width: 1023px) {
    .intro {
      background-image: url(../../img/header_tablet.jpg); } }
  @media (max-width: 1023px) and (min-resolution: 2dppx) {
    .intro {
      background-image: url(../../img/header_tablet@2.jpg); } }
  @media (max-width: 767px) {
    .intro {
      background-image: url(../../img/header_mobile.jpg); } }
  @media (max-width: 767px) and (min-resolution: 2dppx) {
    .intro {
      background-image: url(../../img/header_mobile@2.jpg); } }

.intro__header {
  margin-top: 20px;
  margin-bottom: 230px; }
  @media (max-width: 1023px) {
    .intro__header {
      margin-bottom: 116px; } }

.intro__content {
  color: #ffffff;
  margin-bottom: 275px; }
  @media (max-width: 1023px) {
    .intro__content {
      margin-left: 63px;
      margin-bottom: 163px; } }
  @media (max-width: 767px) {
    .intro__content {
      margin-left: 0;
      margin-bottom: 70px; } }

.intro__title {
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  margin: 0;
  margin-bottom: 31px;
  width: 700px; }
  @media (max-width: 1023px) {
    .intro__title {
      font-size: 60px;
      line-height: 70px;
      width: 400px; } }
  @media (max-width: 767px) {
