.intro__button {
  display: flex;
  width: 407px;
  justify-content: center;
  align-items: center;
  margin-left: 94px;
  background-color: #00B2FF;
  height: 55px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 16px;
  line-height: 19px;
  border-color: #00B2FF; }
  @media (max-width: 1023px) {
    .intro__button {
      margin-left: 50px;
      width: 379px; } }
  @media (max-width: 767px) {
    .intro__button {
      margin: 0;
      width: 100%;
      display: none; } }
  .intro__button--mobile {
    display: none; }
    @media (max-width: 767px) {
      .intro__button--mobile {
