.header {
  display: flex;
  flex-wrap: wrap;
  align-self: center;
  justify-content: space-between; }

.header__content {
  display: flex;
  flex-wrap: wrap; }

.header__list {
  align-self: center;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0 40px;
  padding: 0; }
  @media (max-width: 1023px) {
    .header__list {
      margin-right: 0; } }

.header__item {
  margin-right: 80px; }
  .header__item:last-child {
    margin-right: 0; }
  @media (max-width: 1023px) {
    .header__item {
      margin-right: 33px; } }

.header__link {
  color: #ffffff; }
  .header__link:hover, .header__link:focus {
    opacity: 0.5; }

@media (max-width: 1023px) {
  .header__text,
  .header__button {
    display: none; } }

.header__svg--tablet, .header__svg--mobile {
  display: none; }

@media (max-width: 1023px) {
  .header__svg {
    display: none; }
    .header__svg--tablet, .header__svg--all {
      display: block; } }

@media (max-width: 767px) {
  .header__svg--all {
    display: none; }
  .header__svg--mobile {
    display: flex; } }
